import React from 'react'
import { Container, Row } from 'react-bootstrap'
import SubTitle from '../Uitilty/SubTitle'
import CategoryCard from './CategoryCard'
import img from '../../images/clothe.png'
import Pagenation from '../Uitilty/Pagenation'

const CategoeyContainer = () => {
  return (
   <Container style={{ minHeight: "650px" }}>
    <SubTitle title={' '} btntitle={''} path={'/'}/>
    <Row className="my-2 d-flex justify-content-betwee">
    <CategoryCard img={img} title={' 1'} background={'#b7ffbd'}/>
    <CategoryCard img={img} title={' 2'} background={'#b7ffbd'}/>
    <CategoryCard img={img} title={' 3'} background={'#b7ffbd'}/>
    <CategoryCard img={img} title={' 4'} background={'#b7ffbd'}/>
    <CategoryCard img={img} title={' 5'} background={'#b7ffbd'}/>
    <CategoryCard img={img} title={' 6'} background={'#b7ffbd'}/>
    <CategoryCard img={img} title={' 7'} background={'#b7ffbd'}/>
    <CategoryCard img={img} title={' 8'} background={'#b7ffbd'}/>
    <CategoryCard img={img} title={' 9'} background={'#b7ffbd'}/>
    <CategoryCard img={img} title={' 10'} background={'#b7ffbd'}/>
    <CategoryCard img={img} title={' 11'} background={'#b7ffbd'}/>
    <CategoryCard img={img} title={' 12'} background={'#b7ffbd'}/>
    </Row>
    <Pagenation />
   </Container>
  )
}

export default CategoeyContainer
