import React from 'react'
import { Col, Row } from 'react-bootstrap'

const ProducteText = () => {
  return (
    <div>
    <Row className="mt-4">
      <div className="cat-text mt-4" > :</div>
    </Row>
    <Row>
      <Col md="8">
        <div className="cat-title d-inline">
           XR   128    4G LTE   
           ()  <div className="cat-rate d-inline mx-3">4.5</div>
        </div>
      </Col>
    </Row>
    <Row>
      <Col md="8" className="mt-4">
        <div className="cat-text d-inline"> :</div>
        <div className="barnd-text d-inline mx-1"> </div>
      </Col>
    </Row>
    <Row>
      <Col md="8" className="mt-1 d-flex">
        <div
          className="color ms-2 border"
          style={{ backgroundColor: "#E52C2C" }}></div>
        <div
          className="color ms-2 border "
          style={{ backgroundColor: "white" }}></div>
        <div
          className="color ms-2 border"
          style={{ backgroundColor: "black" }}></div>
      </Col>
    </Row>

    <Row className="mt-4">
      <div className="cat-text"> :</div>
    </Row>
    <Row className="mt-2">
      <Col md="10">
        <div className="product-description d-inline">
             SIM     e-SIM   
                  
                    
             A12        
                   
                 ISP 
                      
                     
              
        </div>
      </Col>
    </Row>
    <Row className="mt-4">
      <Col md="12 d-flex justify-content-start">
        <div className="product-price d-inline px-3 py-3 border">34000 </div>
        <div className="product-cart-add px-3 py-3 d-inline mx-3"> </div>
      </Col>
    </Row>
  </div>
  )
}

export default ProducteText
